// 												COURSE
.course {
	padding: 1.7em 2em;
	background-color: $white;
	box-shadow: 0 20px 56px -33px rgba($black, 0.2);
	min-height: 300px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	max-width: 317px;
	width: 100%;
	border-radius: 10px;
	transition: background-color .4s, color .4s, box-shadow .4s;
	@include hover {
		background-color: $orange;
		color: $white;
		box-shadow: 0 25px 42px -14px $orange;
		.course__aside {
			color: $white; }
		.course__price:before {
			background-color: $white; }
		.course__title.title {
			color: $white; } }
	&__aside {
		color: $grayLight;
		transition: color .4s; }
	&__bottom {
		display: flex;
		padding-top: 1em;
		@include f(1.2em, 700);
		letter-spacing: normal; }
	&__price {
		margin-left: 30px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			@include circle(7);
			background-color: $black;
			left: -18.5px;
			top: 50%;
			margin-top: -3.5px;
			transition: background-color .4s; } }
	&__title {
		margin-top: .4em;
		transition: color .4s; } }

// 												EVENT
.event {
	max-width: 433px;
	width: 100%;
	&__img {
		img {
			max-width: 100%;
			display: block; } }
	&__date {
		font-size: 16px;
		line-height: 1.75;
		padding-top: 1.25em;
		letter-spacing: normal;
		span {
			text-transform: uppercase;
			color: $orange;
			font-weight: 700; } }
	&__title {
		max-width: 300px;
		margin-top: 0.25em; } }

// 										INSTRUCTOR
.instructor {
	max-width: 433px;
	width: 100%;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	overflow: hidden;
	&__top {
		background-color: $blue;
		padding-bottom: 34%;
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			top: 1.33em;
			left: 2em;
			right: 2em;
			bottom: -1em;
			border: 1px solid $white;
			border-radius: 10px; } }
	&__bottom {
		text-align: center;
		padding: 0 2em 2.66em;
		p {
			text-align: left;
			line-height: 1.9; } }
	&__img {
		@include circle(120);
		@include cover;
		margin: -5.7em auto 0;
		overflow: hidden;
		z-index: 1;
		position: relative; }
	&__title {
		margin-top: 0.5em; }
	&__aside {
		letter-spacing: 0.063em;
		color: $grayLight;
		margin-bottom: 1em; } }

// 									TESTIMONIAL
.testimonial {
	padding: 2em 2em 2.66em;
	max-width: 433px;
	width: 100%;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	&__top {
		display: flex;
		align-items: center; }
	&__img {
		flex-shrink: 0;
		@include circle(90);
		@include cover;
		overflow: hidden;
		margin-right: 1.33em; }
	&__aside {
		color: $grayLight;
		letter-spacing: 0.033em; }
	&__bottom {
		position: relative;
		margin-top: 1.6em;
		&:before {
			content: '';
			position: absolute;
			width: 54px;
			height: 38px;
			background-image: url(../img/quotes.svg);
			@include cover;
			top: 9px;
			left: 0;
			z-index: -1; }
		p {
			&:first-child {
				padding: 0 2em; } } } }
