html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 400;
  src: local("Verdana"), local("Verdana"), url("../fonts/Verdana.woff2") format("woff2"), url("../fonts/Verdana.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 400;
  src: local("Verdana-Italic"), local("Verdana-Italic"), url("../fonts/Verdana-Italic.woff2") format("woff2"), url("../fonts/Verdana-Italic.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 700;
  src: local("Verdana-Bold"), local("Verdana-Bold"), url("../fonts/Verdana-Bold.woff2") format("woff2"), url("../fonts/Verdana-Bold.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 700;
  src: local("Verdana-BoldItalic"), local("Verdana-BoldItalic"), url("../fonts/Verdana-BoldItalic.woff2") format("woff2"), url("../fonts/Verdana-BoldItalic.woff") format("woff");
}

.icon-quotes {
  width: 1.42em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  line-height: 1.90;
  letter-spacing: 0.063em;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #383838;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: currentColor;
}

a:hover {
  text-decoration: none;
}

.inner {
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 30px;
}

.course {
  padding: 1.7em 2em;
  background-color: #fff;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 317px;
  width: 100%;
  border-radius: 10px;
  transition: background-color .4s, color .4s, box-shadow .4s;
}

.no-touch .course:hover {
  background-color: #ff833e;
  color: #fff;
  box-shadow: 0 25px 42px -14px #ff833e;
}

.no-touch .course:hover .course__aside {
  color: #fff;
}

.no-touch .course:hover .course__price:before {
  background-color: #fff;
}

.no-touch .course:hover .course__title.title {
  color: #fff;
}

.course__aside {
  color: #9d9d9d;
  transition: color .4s;
}

.course__bottom {
  display: flex;
  padding-top: 1em;
  font-size: 1.2em;
  font-weight: 700;
  letter-spacing: normal;
}

.course__price {
  margin-left: 30px;
  position: relative;
}

.course__price:before {
  content: '';
  position: absolute;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background-color: #000;
  left: -18.5px;
  top: 50%;
  margin-top: -3.5px;
  transition: background-color .4s;
}

.course__title {
  margin-top: .4em;
  transition: color .4s;
}

.event {
  max-width: 433px;
  width: 100%;
}

.event__img img {
  max-width: 100%;
  display: block;
}

.event__date {
  font-size: 16px;
  line-height: 1.75;
  padding-top: 1.25em;
  letter-spacing: normal;
}

.event__date span {
  text-transform: uppercase;
  color: #ff833e;
  font-weight: 700;
}

.event__title {
  max-width: 300px;
  margin-top: 0.25em;
}

.instructor {
  max-width: 433px;
  width: 100%;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
}

.instructor__top {
  background-color: #0053a5;
  padding-bottom: 34%;
  position: relative;
  overflow: hidden;
}

.instructor__top:before {
  content: '';
  position: absolute;
  top: 1.33em;
  left: 2em;
  right: 2em;
  bottom: -1em;
  border: 1px solid #fff;
  border-radius: 10px;
}

.instructor__bottom {
  text-align: center;
  padding: 0 2em 2.66em;
}

.instructor__bottom p {
  text-align: left;
  line-height: 1.9;
}

.instructor__img {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin: -5.7em auto 0;
  overflow: hidden;
  z-index: 1;
  position: relative;
}

.instructor__title {
  margin-top: 0.5em;
}

.instructor__aside {
  letter-spacing: 0.063em;
  color: #9d9d9d;
  margin-bottom: 1em;
}

.testimonial {
  padding: 2em 2em 2.66em;
  max-width: 433px;
  width: 100%;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.testimonial__top {
  display: flex;
  align-items: center;
}

.testimonial__img {
  flex-shrink: 0;
  height: 90px;
  width: 90px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  overflow: hidden;
  margin-right: 1.33em;
}

.testimonial__aside {
  color: #9d9d9d;
  letter-spacing: 0.033em;
}

.testimonial__bottom {
  position: relative;
  margin-top: 1.6em;
}

.testimonial__bottom:before {
  content: '';
  position: absolute;
  width: 54px;
  height: 38px;
  background-image: url(../img/quotes.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  top: 9px;
  left: 0;
  z-index: -1;
}

.testimonial__bottom p:first-child {
  padding: 0 2em;
}

.title {
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 44px;
  font-weight: 700;
  line-height: 1.23;
  color: #000;
  letter-spacing: normal;
}

.title_lg {
  font-size: 30px;
  line-height: 1.48;
}

.title_md {
  font-size: 21px;
  line-height: 1.48;
}
